.trainers {
  margin-top: 120px;
  margin-bottom: 120px;
}

.trainers__list {
  @include markerless;

  display: flex;
  flex-wrap: wrap;


  &--js-active {
    flex-wrap: nowrap;
    justify-content: space-between;
  }
}

.trainers__list li {
  width: 100%;
  max-width: 260px;

  margin-right: 40px;
  margin-bottom: 40px;

  background-color: $trainer-card-background;

  &:nth-child(4n),
  &:last-child {
    margin-right: 0;
  }
}

.trainers__list--js-active li {
  max-width: 100%;

  margin-right: 0;
  margin-bottom: 0;
}


/*
==================================
--------- МЕДИАВЫРАЖЕНИЯ ---------
==================================
*/

// *** ширина вьюпорта "Планшет" ***
@media (max-width: $width--desktop) {
  .trainers {
    margin-top: 100px;
  }

  .trainers__title {
    text-align: center;
  }

  .trainers__list li {
    max-width: 268px;

    margin-right: 30px;
    margin-bottom: 30px;


    &:nth-child(2n) {
      margin-right: 0;
    }
  }

  .trainers__list--js-active li {
    margin-right: 0;
    margin-bottom: 0;
  }

  .trainers__slides-container {
    max-width: 566px;
  }
}


// *** ширина вьюпорта "Мобильный" ***
@media (max-width: $width--tablet) {
  .trainers__list li {
    max-width: 100%;

    margin-right: 0;
    margin-bottom: 20px;


    &:last-child {
      margin-bottom: 0;
    }
  }

  .trainers__list--js-active li {
    margin-right: 0;
    margin-bottom: 0;
  }

  .trainers__slides-container {
    max-width: 320px;
  }
}
